import { Injectable, isDevMode } from "@angular/core";
import { BehaviorSubject } from "rxjs/BehaviorSubject";

@Injectable()
export class SigninService {

    private _isAuthenticated = new BehaviorSubject<boolean>(false); // false is your initial value
    isAuthenticated$ = this._isAuthenticated.asObservable();
    private _idToken = "";
    private _userDisplayName = "";

    constructor() {
        if (isDevMode()) {
            this._userDisplayName = "John Doe";
            this._idToken =
                "eyJhbGciOiJSUzI1NiIsImtpZCI6ImRlOTdmMzA1MTRjOWU4NThiYzA0ODc3NjNmNzNmN2UwYzVmYTBhYjEifQ..0Y68-LiCtOVyp-lNNQkCDTal0XZ3-LGpGxFTJOmPAyQgVQru5UrPU43OtfwN4LgqgMKNBqCRFg98-KZYLjFnG7PQTLKesoxvLPOChGFKdl-NZRGXqiuSGckrkAb9k-KYyvZFFaXNXYBjB7HrdCTm645_30HqkOiVPx8syE_JRZjCYtqgapbSFuGagNBMb_wiejY7_bnJO8KrWQTLD17uPqe7yM2ZKnLKGPTyNZIoKSwKUf0rot4rGvted0OS2f4SFseyNpChpcc_9xUQM-GTH5w2FnI0J3ijeV7HjD2ujKKe_PX4WMq3gbtCX47lUbidkrmEqhnEjkKQ7KSmhzFC_g";
            this._isAuthenticated.next(true);
        }
    }

    signOut() {
        this._userDisplayName = "";
        this._idToken = "";
        this._isAuthenticated.next(false);
    }

    get isAuthenticated(): boolean {
        return this._isAuthenticated.getValue();
    }

    set isAuthenticated(value: boolean) {
        this._isAuthenticated.next(value);
    }

    get idToken(): string {
        return this._idToken;
    }

    set idToken(value: string) {
        this._idToken = value;
    }

    get userDisplayName(): string {
        return this._userDisplayName;
    }

    set userDisplayName(value: string) {
        this._userDisplayName = value;
    }
}


