import { ElementRef, Component, OnInit, Input } from "@angular/core";

@Component({
    selector: "app-vert-navigation-item",
    templateUrl: "./app-vert-navigation-item.component.html",
    styleUrls: ["./app-vert-navigation-item.component.css"]
})
export class AppVertNavigationItemComponent implements OnInit {

    @Input()
    icon: string;
    @Input()
    title: string;
    @Input()
    link: string;

    constructor(private _el: ElementRef) {
        //
    }

    ngOnInit() {
        const nativeElement: HTMLElement = this._el.nativeElement;
        const parentElement = nativeElement.parentElement;

        // move all children out of the element
        while (nativeElement.firstChild) {
            parentElement.insertBefore(nativeElement.firstChild, nativeElement);
        }

        // remove the empty element(the host)
        parentElement.removeChild(nativeElement);
    }
}


