import { BrowserModule } from "@angular/platform-browser";
import { NgModule } from "@angular/core";
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
import { AppComponent } from "./app.component";
import { AppNavigationComponent } from "./navigation/app-navigation/app-navigation.component";
import { RouterModule, Routes } from "@angular/router";
import { AppVertNavigationItemComponent } from "./navigation/app-vert-navigation-item/app-vert-navigation-item.component";
import { SigninService } from "./signin.service";
//pages
import { AppPageComponent } from "./pages/app-page/app-page.component";
import { AppPageAppsComponent } from "./pages/app-page-apps/app-page-apps.component";
import { AppPageDatasourcesComponent } from "./pages/app-page-datasources/app-page-datasources.component";
import { AppPageModelsComponent } from "./pages/app-page-models/app-page-models.component";
import { AppPageViewsComponent } from "./pages/app-page-views/app-page-views.component";
import { AppPageListsComponent } from "./pages/app-page-lists/app-page-lists.component";
import { AppPageFormsComponent } from "./pages/app-page-forms/app-page-forms.component";
import { AppPageSigninComponent } from "./pages/app-page-signin/app-page-signin.component";
import { SigninComponent } from './signin/signin.component';

const appRoutes: Routes = [
    { path: "", component: AppPageComponent },
    { path: "apps", component: AppPageAppsComponent },
    { path: "datasources", component: AppPageDatasourcesComponent },
    { path: "models", component: AppPageModelsComponent },
    { path: "views", component: AppPageViewsComponent },
    { path: "lists", component: AppPageListsComponent },
    { path: "forms", component: AppPageFormsComponent },
    { path: "signin", component: AppPageSigninComponent }
];

@NgModule({
    declarations: [
        AppComponent,
        AppNavigationComponent,
        AppPageComponent,
        AppPageAppsComponent,
        AppPageDatasourcesComponent,
        AppPageModelsComponent,
        AppPageViewsComponent,
        AppPageListsComponent,
        AppPageFormsComponent,
        AppVertNavigationItemComponent,
        AppPageSigninComponent,
        AppPageSigninComponent,
        SigninComponent
    ],
    imports: [
        BrowserModule,
        FormsModule,
        HttpModule,
        RouterModule.forRoot(appRoutes, { useHash: true })
    ],
    providers: [SigninService],
    bootstrap: [AppComponent]
})
export class AppModule {
    //
}


