import { Component } from "@angular/core";
import { SigninService } from "./signin.service";
import { Subscription } from "rxjs";

export class BaseComponent {
    private _isAuthenticated = false;
    private _subscription: Subscription;

    constructor(private _signinService: SigninService) {
        //
    }

    get signinService(): SigninService {
        return this._signinService;
    }

    get isAuthenticated(): boolean {
        return this._isAuthenticated;
    }

    get userAuthToken(): string {
        return this._signinService.idToken;
    }

    get userDisplayName(): string {
        return this._signinService.userDisplayName;
    }

    signOut() {
        this._signinService.signOut();
    }

    init() {
        this._subscription = this.signinService.isAuthenticated$.subscribe(isAuthenticated => {
            this._isAuthenticated = isAuthenticated;
        });
    }

    dispose() {
        if (this._subscription) {
            this._subscription.unsubscribe();
        }
    }
}


